import React, { useState, useEffect } from 'react'


function Filter({setCname,setRname}) {

    // const [filtered, setFiltered] = useState('')
    const [region, setRegion] = useState('')
    const [name, setName] = useState('')

    const getSearch = e =>{
        e.preventDefault()
        console.log(name)
        setCname(name)
    }

    useEffect(() => {
        setRname(region)
    }, [region])
    return (
        <section className='filter'>
            <form onSubmit={(e)=>getSearch(e)}>
                <input type="text" placeholder='Search for a country' value={name} onChange={e=>{setName(e.target.value)}}/>
            </form>
            <div className='region-filter'>
                <select name="select" id="select" className='select' onChange={(e)=>{setRegion(e.target.value)}}>
                    <option value="">Filter By Region</option>
                    <option value="asia">Asia</option>
                    <option value="africa">Africa</option>
                    <option value="america">America</option>
                    <option value="europe">Europe</option>
                    <option value="oceania">Oceania</option>
                </select>
            </div>
        </section>
    )
}

export default Filter
