import React,{useEffect, useState} from 'react'
import Countries from './components/Countries';
import Filter from './components/Filter';
import Header from './components/Header';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom'
import Country from './components/Country';
import Loader from './components/Loader';



function App() {

  const [cname, setCname] = useState('')      // Country Name
  const [rname, setRname] = useState('')      // Region Name
  const [val, setVal] = useState(true)

  useEffect(() => {
    setTimeout(() => {
      setVal(false)
    }, 2500);
  }, [])

  return (
    <Router>
      <div className="App">
        {
          val && <Loader />
        }
        <Header />
        <Switch>
          <Route path='/' exact>
            <Filter setCname={setCname} setRname={setRname} />
            <Countries cname={cname} rname={rname} />
          </Route>
          <Route path='/countries/:name' component={Country}/>
        </Switch>

      </div>
    </Router>
  );
}

export default App;
