import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'

const url = 'https://restcountries.com/v3.1/all'

function Countries({ cname, rname }) {
  const [countries, setCountries] = useState([])

  const fetchCountryData = async () => {
    const response = await fetch(url)
    const countries = await response.json()
    console.log(countries)
    setCountries(countries)
  }

  useEffect(() => {
    fetchCountryData()
  }, [])

  if (cname === '' && rname === '') {
    return (
      <section className="grid">
        {countries.map((country, idx) => {
          const {
            name,
            population,
            region,
            capital,
            flags,
            numericCode,
          } = country
          return (
            <Link
              to={`/countries/${name.common}`}
              style={{ textDecoration: 'none' }}
              key={idx}
            >
              <article key={numericCode} className="country-card">
                <img src={flags.png} alt="Nation-img" />
                <div className="details">
                  <h3>Country : {name.common}</h3>
                  <h4>Population : {population}</h4>
                  <h4>Region : {region}</h4>
                  <h4>Capital : {capital}</h4>
                </div>
              </article>
            </Link>
          )
        })}
      </section>
    )
  }
  if (rname !== '') {
    const searchedCountry = countries.filter(
      (country) => country.region.toLowerCase() === rname.toLowerCase(),
    )
    return (
      <section className="grid">
        {searchedCountry.map((filteredCountry, idx) => {
          const {
            name,
            population,
            region,
            capital,
            flags,
            numericCode,
          } = filteredCountry
          return (
            <Link
              to={`/countries/${name.common}`}
              style={{ textDecoration: 'none' }}
              key={idx}
            >
              <article key={numericCode} className="country-card">
                <img src={flags.png} alt="Nation-img" />
                <div className="details">
                  <h3>Country : {name.common}</h3>
                  <h4>Population : {population}</h4>
                  <h4>Region : {region}</h4>
                  <h4>Capital : {capital}</h4>
                </div>
              </article>
            </Link>
          )
        })}
      </section>
    )
  } else {
    const searchedCountry = countries.filter(
      (country) => country.name.common.toLowerCase() === cname.toLowerCase(),
    )
    return (
      <section className="grid">
        {searchedCountry.map((filteredCountry, idx) => {
          const {
            name,
            population,
            region,
            capital,
            flags,
            numericCode,
          } = filteredCountry
          return (
            <Link
              to={`/countries/${name.common}`}
              style={{ textDecoration: 'none' }}
              key={idx}
            >
              <article key={numericCode} className="country-card">
                <img src={flags.png} alt="Nation-img" />
                <div className="details">
                  <h3>Country : {name.common}</h3>
                  <h4>Population : {population}</h4>
                  <h4>Region : {region}</h4>
                  <h4>Capital : {capital}</h4>
                </div>
              </article>
            </Link>
          )
        })}
      </section>
    )
  }
}

export default Countries
