import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'



function Country({ match }) {

    const [country, setCountry] = useState([])

    const fetchCountry = async () => {
        const response = await fetch(`https://restcountries.com/v3.1/name/${match.params.name}`)
        const item = await response.json()
        console.log(item)
        setCountry(item)
    }

    useEffect(() => {
        fetchCountry()
    }, [])

    return (
        <section className='flexbox'>
            <Link to='/'>
                <button><i className="fas fa-chevron-circle-left"></i> Back</button>
            </Link>
            {country.map((c) => {
                const { ccn3, flags, name, population
                    , region, subregion, capital, timezones } = c
                    console.log(name.common, population)

                return (
                    <article key={ccn3}>
                        <img src={flags.png} alt="Nation-img" />
                        <div className="details">
                            <div className="title-div">
                                <h2>{name.common}</h2>
                                <h4>Population : <span>{population}</span></h4>
                                <h4>Region : <span>{region}</span></h4>
                                <h4>Sub Region : <span>{subregion}</span></h4>
                                <h4>Capital : <span>{capital}</span></h4>
                            </div>
                            <div className="info-div">
                                <h4>Time Zone: <span>{timezones} </span></h4>
                            </div>
                        </div>
                    </article>
                )
            })}
        </section>
    )
}

export default Country

